'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

/*
* The following modules are intentionally not exported: evalpoly-compile, evalpoly-compile-c, evalrational-compile, evalrational-compile-c
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name continuedFraction
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/tools/continued-fraction}
*/
import continuedFraction from '@stdlib/math-base-tools-continued-fraction';
setReadOnly( ns, 'continuedFraction', continuedFraction );

/**
* @name evalpoly
* @memberof tools
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/tools/evalpoly}
*/
import evalpoly from '@stdlib/math-base-tools-evalpoly';
setReadOnly( ns, 'evalpoly', evalpoly );

/**
* @name evalrational
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/tools/evalrational}
*/
import evalrational from '@stdlib/math-base-tools-evalrational';
setReadOnly( ns, 'evalrational', evalrational );

/**
* @name fibpoly
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/base/tools/fibpoly}
*/
import fibpoly from '@stdlib/math-base-tools-fibpoly';
setReadOnly( ns, 'fibpoly', fibpoly );

